.contact {
  @include side-positioned;

  &__text {
    font-size: 24px;
    width: 280 px;
  }

  &__form {
    width: 380px;
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .contact {
    @include centered-column;
    
    &__text,
    &__form {
      width: 100%;
    }
  }
}
